.notification{
    transition: all 0.6s ease-in-out;
    height: 30px;
    min-height: 30px;
    width: 15%;
    display: block;
    float: left;
    border-radius: 5px;
   font-weight: bold;
   color: white;
   padding: 3px;
   margin: 2px;
   text-align: left;
    opacity: 0;
}
  
.active {
  opacity: 0.8;
  }

.added{
    background-color: green;
  }

.deleted{
        background-color: red;

  }

  .saved{
  background-color: blue;
}

.notification-icon{
    padding: 3px;
    margin-left: 10px;
  }